import { Link } from 'gatsby'
import PropTypes from 'prop-types'
import React from 'react'
import styled from 'styled-components'
import { medium, heavy } from './colors';

import Navigation from '../components/navigation'

const SubTitle = styled.span`
  margin-right: 12px;
  font-weight: 500;
  color: ${medium};

  &:hover {
    border-bottom: 2px solid ${medium};
  }
`;

const BigTitle = styled.h1`
  color: ${heavy};
  margin: 0;
`

const Header = ({ siteTitle }) => (
  <div>
    <div
      style={{
        margin: `0 auto`,
        maxWidth: 700,
        paddingTop: '1.45rem',
        paddingBottom: '0rem',
        paddingLeft: '1rem',
        paddingRight: '1rem',
      }}
    >
      <BigTitle>
        {siteTitle}
      </BigTitle>
      <Navigation>
        <Link
          to="/"
          style={{
            textDecoration: 'none',
          }}
        >
          <SubTitle>首页</SubTitle>
        </Link>
        <Link
          to="/aboutme"
          style={{
            textDecoration: 'none',
          }}
        >
          <SubTitle>关于我</SubTitle>
        </Link>
      </Navigation>
    </div>
  </div>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
