class Disqus extends Component {

  componentDidMount() {
    const { pageId, pageUrl } = this.props;
    window.disqus_config = function () {
      this.page.url = pageUrl;
      this.page.identifier = pageId;
    }
    eval(`
      (function() { // DON'T EDIT BELOW THIS LINE
      var d = document, s = d.createElement('script');
      s.src = 'https://vincentdchan.disqus.com/embed.js';
      s.setAttribute('data-timestamp', +new Date());
      (d.head || d.body).appendChild(s);
      })();
    `)
  }

  componentWillUnmount() {
    delete window.disqus_config;
  }

  render() {
    return <div id="disqus_thread" />
  }

}

export default function Template({
  data, // this prop will be injected by the GraphQL query below.
}) {
  const { markdownRemark } = data // data.markdownRemark holds our post data
  const { frontmatter, html } = markdownRemark
  let path = frontmatter.path;
  if (!path) {
    const __date = new Date(frontmatter.date);
    const year = __date.getFullYear().toString();
    let month = (__date.getMonth() + 1).toString();
    if (month.length < 2) {
      month = '0' + month;
    }
    path = `/${year}/${month}/${frontmatter.permalink}`;
  }
  return (
    <Layout
      headerImage={
        frontmatter.headerImage && (
          <ProgressiveImage src={frontmatter.headerImage} placeholder="">
            {(src, loading) => {
              return loading ? <HeaderImagePlaceFolder /> : (
                <img
                  src={src}
                  alt=""
                  style={{
                    margin: '0px',
                  }}
                />
              );
            }}
          </ProgressiveImage>
        )
      }
    >
      <SEO
        title={frontmatter.title}
        keywords={[
          `blog`,
          `technology`,
          `photography`,
        ]}
      />
      <div className="blog-post-container">
        <BlogPost>
          <Title to={path}>
            {frontmatter.title}
          </Title>
          <PostMeta>
            Posted at {betterDate(frontmatter.date)}
          </PostMeta>
          <div
            className="blog-post-content"
            dangerouslySetInnerHTML={{ __html: html }}
          />
        </BlogPost>
      </div>
      <Disqus
        pageUrl={`https://diverse.space${path}`}
        pageId={path}
      />
      {/* <div
        className="disqus-container"
        dangerouslySetInnerHTML={{ __html: disqusCode({
          pageUrl: `https://diverse.space${path}`,
          pageId: frontmatter.title,
        }) }}
      /> */}
    </Layout>
  )
}

export const pageQuery = graphql`
  query($title: String!) {
    markdownRemark(frontmatter: { title: { eq: $title } }) {
      html
      frontmatter {
        date
        path
        title
        permalink
        headerImage
      }
    }
  }
`
