import React from 'react'
import { Link } from 'gatsby'
import styled from 'styled-components'
import { heavy } from '../components/colors'

const TitleContainer = styled.span`
  font-weight: 500;
  font-size: 24px;
  color: ${heavy};

  &:hover {
    border-bottom: solid ${heavy} 2px;
  }
`

const Title = ({ to, children }) => (
  <Link
    to={to}
    style={{
      textDecoration: 'none',
    }}
  >
    <TitleContainer>
      {children}
    </TitleContainer>
  </Link>
)

export default Title
